.ratings {
  position: relative;
  text-align: left;
  white-space: normal;
  height: 100%;
}
.ratings .overview {
  display: flex;
  padding: 18px 0;
}
.ratings .overview .overview-left {
  flex: 0 0 137px;
  padding: 6px 0;
  width: 137px;
  border-right: 1px solid #d9dde1;
  text-align: center;
}
@media only screen and (max-width: 320px) {
  .ratings .overview .overview-left {
    flex: 0 0 120px;
    width: 120px;
  }
}
.ratings .overview .overview-left .score {
  margin-bottom: 6px;
  line-height: 28px;
  font-size: 24px;
  color: #fc9153;
}
.ratings .overview .overview-left .title {
  margin-bottom: 8px;
  line-height: 12px;
  font-size: 12px;
  color: #333;
}
.ratings .overview .overview-left .rank {
  line-height: 10px;
  font-size: 10px;
  color: #999;
}
.ratings .overview .overview-right {
  flex: 1;
  padding: 6px 0 6px 24px;
}
@media only screen and (max-width: 320px) {
  .ratings .overview .overview-right {
    padding-left: 6px;
  }
}
.ratings .overview .overview-right .score-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}
.ratings .overview .overview-right .score-wrapper .title {
  line-height: 18px;
  font-size: 12px;
  color: #333;
}
.ratings .overview .overview-right .score-wrapper .star {
  margin: 0 12px;
}
.ratings .overview .overview-right .score-wrapper .score {
  line-height: 18px;
  font-size: 12px;
  color: #fc9153;
}
.ratings .overview .overview-right .delivery-wrapper {
  display: flex;
  align-items: center;
}
.ratings .overview .overview-right .delivery-wrapper .title {
  line-height: 18px;
  font-size: 12px;
  color: #333;
}
.ratings .overview .overview-right .delivery-wrapper .delivery {
  margin-left: 12px;
  font-size: 12px;
  color: #999;
}
.ratings .rating-wrapper {
  padding: 0 18px;
}
.ratings .rating-wrapper .rating-item {
  display: flex;
  padding: 18px 0;
}
.ratings .rating-wrapper .rating-item:last-child::before {
  display: none;
}
.ratings .rating-wrapper .rating-item:last-child::after {
  display: none;
}
.ratings .rating-wrapper .rating-item .avatar {
  flex: 0 0 28px;
  width: 28px;
  margin-right: 12px;
}
.ratings .rating-wrapper .rating-item .avatar img {
  height: auto;
  border-radius: 50%;
}
.ratings .rating-wrapper .rating-item .content {
  position: relative;
  flex: 1;
}
.ratings .rating-wrapper .rating-item .content .name {
  margin-bottom: 4px;
  line-height: 12px;
  font-size: 10px;
  color: #333;
}
.ratings .rating-wrapper .rating-item .content .star-wrapper {
  margin-bottom: 6px;
  display: flex;
  align-items: center;
}
.ratings .rating-wrapper .rating-item .content .star-wrapper .star {
  margin-right: 6px;
}
.ratings .rating-wrapper .rating-item .content .star-wrapper .delivery {
  font-size: 10px;
  color: #999;
}
.ratings .rating-wrapper .rating-item .content .text {
  margin-bottom: 8px;
  line-height: 18px;
  color: #333;
  font-size: 12px;
}
.ratings .rating-wrapper .rating-item .content .recommend {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  line-height: 16px;
}
.ratings .rating-wrapper .rating-item .content .recommend .icon-thumb_up,
.ratings .rating-wrapper .rating-item .content .recommend .item {
  margin: 0 8px 4px 0;
  font-size: 10px;
}
.ratings .rating-wrapper .rating-item .content .recommend .icon-thumb_up {
  color: #00a0dc;
}
.ratings .rating-wrapper .rating-item .content .recommend .item {
  padding: 0 6px;
  border: 1px solid rgba(7,17,27,0.1);
  border-radius: 1px;
  color: #999;
  background: #fff;
}
.ratings .rating-wrapper .rating-item .content .time {
  position: absolute;
  top: 0;
  right: 0;
  line-height: 12px;
  font-size: 12px;
  color: #999;
}
/*# sourceMappingURL=src/components/ratings/ratings.css.map */