.goods {
  position: relative;
  height: 100%;
}
.goods .scroll-nav-wrapper {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 48px;
}
.goods >>>.cube-scroll-nav-bar {
  width: 80px;
  white-space: normal;
  overflow: hidden;
}
.goods >>>.cube-scroll-nav-bar-item {
  padding: 0 10px;
  display: flex;
  align-items: center;
  height: 56px;
  line-height: 14px;
  font-size: 12px;
  background-color: #f3f5f7;
}
.goods >>>.cube-scroll-nav-bar-item .text {
  flex: 1;
  position: relative;
}
.goods >>>.cube-scroll-nav-bar-item .num {
  position: absolute;
  right: -8px;
  top: -10px;
}
.goods >>>.cube-scroll-nav-bar-item .support-icon {
  display: inline-block;
  vertical-align: top;
  margin-right: 4px;
}
.goods >>>.cube-scroll-nav-bar-item_active {
  background-color: #fff;
  color: #333;
}
.goods >>>.cube-scroll-nav-panel-title {
  padding-left: 14px;
  height: 26px;
  line-height: 26px;
  border-left: 2px solid #d9dde1;
  font-size: 12px;
  color: #666;
  background-color: #f3f5f7;
}
.goods .food-item {
  display: flex;
  margin: 18px;
  padding-bottom: 18px;
  position: relative;
}
.goods .food-item:last-child {
  margin-bottom: 0;
}
.goods .food-item:last-child::before {
  display: none;
}
.goods .food-item:last-child::after {
  display: none;
}
.goods .food-item .icon {
  flex: 0 0 57px;
  margin-right: 10px;
}
.goods .food-item .icon img {
  height: auto;
}
.goods .food-item .content {
  flex: 1;
}
.goods .food-item .content .name {
  margin: 2px 0 8px 0;
  height: 14px;
  line-height: 14px;
  font-size: 14px;
  color: #333;
}
.goods .food-item .content .desc,
.goods .food-item .content .extra {
  line-height: 10px;
  font-size: 10px;
  color: #999;
}
.goods .food-item .content .desc {
  line-height: 12px;
  margin-bottom: 8px;
}
.goods .food-item .content .extra .count {
  margin-right: 12px;
}
.goods .food-item .content .price {
  font-weight: 700;
  line-height: 24px;
}
.goods .food-item .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.goods .food-item .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #999;
}
.goods .food-item .cart-control-wrapper {
  position: absolute;
  right: 0;
  bottom: 12px;
}
.goods .shop-cart-wrapper {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 48px;
}
/*# sourceMappingURL=src/components/goods/goods.css.map */