





































































































































































































































@import "~common/stylus/mixin"
@import "~common/stylus/variable"

.shopcart
  height 100%
  .content
    display flex
    background-color $color-background
    font-size 0
    color $color-light-grey
    .content-left
      flex 1
      .logo-wrapper
        display inline-block
        vertical-align top
        position relative
        top -10px
        margin: 0 12px
        padding: 6px
        width 56px
        height 56px
        box-sizing border-box
        border-radius 50%
        background-color $color-background
        .logo
          width 100%
          height 100%
          border-radius 50%
          text-align center
          background-color $color-dark-grey
          &.highlight
            background-color $color-blue
          .icon-shopping_cart
            line-height 44px
            font-size $fontsize-large-xxx
            color $color-light-grey
            &.highlight
              color $color-white
        .num
          position absolute
          top 0
          right 0
      .price
        display inline-block
        vertical-align top
        margin-top 12px
        line-height 24px
        padding-right 12px
        box-sizing border-box
        border-right 1px solid rgba(255,255,255,0.1)
        font-weight 700
        font-size $fontsize-large
        &.highlight
          color $color-white
      .desc
        display inline-block
        vertical-align top
        margin 12px 0 0 12px
        line-height 24px
        font-size $fontsize-small-s
    .content-right
      flex 0 0 105px
      width 105px
      .pay
        height 48px
        line-height 48px
        text-align center
        font-weight 700
        font-size $fontsize-small
        &.not-enough
          background-color $color-dark-grey
        &.enough
          background-color $color-green
          color $color-white
  .ball-container
    .ball
      position fixed
      left 32px
      bottom 22px
      z-index 200
      transition all 0.4s cubic-bezier(0.49,-0.29,0.75,0.41)
      .inner
        width 16px
        height 16px
        border-radius 50%
        background-color $color-blue
        transition all 0.4s linear
