.seller {
  height: 100%;
  text-align: left;
}
.seller .overview {
  position: relative;
  padding: 18px;
}
.seller .overview .title {
  margin-bottom: 8px;
  line-height: 14px;
  font-size: 14px;
  color: #333;
}
.seller .overview .desc {
  display: flex;
  align-items: center;
  padding-bottom: 18px;
}
.seller .overview .desc .star {
  margin-right: 8px;
}
.seller .overview .desc .text {
  margin-right: 12px;
  line-height: 18px;
  font-size: 10px;
  color: #666;
}
.seller .overview .remark {
  display: flex;
  padding-top: 18px;
}
.seller .overview .remark .block {
  flex: 1;
  text-align: center;
  border-right: 1px solid #d9dde1;
}
.seller .overview .remark .block:last-child {
  border: none;
}
.seller .overview .remark .block h2 {
  margin-bottom: 4px;
  line-height: 10px;
  font-size: 10px;
  color: #999;
}
.seller .overview .remark .block .content {
  line-height: 24px;
  font-size: 10px;
  color: #333;
}
.seller .overview .remark .block .content .stress {
  font-size: 24px;
}
.seller .overview .favorite {
  position: absolute;
  width: 50px;
  right: 11px;
  top: 18px;
  text-align: center;
}
.seller .overview .favorite .icon-favorite {
  display: block;
  margin-bottom: 4px;
  line-height: 24px;
  font-size: 24px;
  color: #ccc;
}
.seller .overview .favorite .icon-favorite.active {
  color: #f01414;
}
.seller .overview .favorite .text {
  line-height: 10px;
  font-size: 10px;
  color: #666;
}
.seller .bulletin {
  padding: 18px 18px 0 18px;
  white-space: normal;
}
.seller .bulletin .title {
  margin-bottom: 8px;
  line-height: 14px;
  color: #333;
  font-size: 14px;
}
.seller .bulletin .content-wrapper {
  padding: 0 12px 16px 12px;
}
.seller .bulletin .content-wrapper .content {
  line-height: 24px;
  font-size: 12px;
  color: #f01414;
}
.seller .bulletin .supports .support-item {
  display: flex;
  align-items: center;
  padding: 16px 12px;
}
.seller .bulletin .supports .support-item:last-child::before {
  display: none;
}
.seller .bulletin .supports .support-item:last-child::after {
  display: none;
}
.seller .bulletin .supports .support-ico {
  margin-right: 6px;
}
.seller .bulletin .supports .text {
  line-height: 16px;
  font-size: 12px;
  color: #333;
}
.seller .pics {
  padding: 18px;
}
.seller .pics .title {
  margin-bottom: 12px;
  line-height: 14px;
  color: #333;
  font-size: 14px;
}
.seller .pics .pic-wrapper {
  display: flex;
  align-items: center;
}
.seller .pics .pic-wrapper .pic-list .pic-item {
  display: inline-block;
  margin-right: 6px;
  width: 120px;
  height: 90px;
}
.seller .pics .pic-wrapper .pic-list .pic-item:last-child {
  margin: 0;
}
.seller .info {
  padding: 18px 18px 0 18px;
  color: #333;
}
.seller .info .title {
  padding-bottom: 12px;
  line-height: 14px;
  font-size: 14px;
}
.seller .info .info-item {
  padding: 16px 12px;
  line-height: 16px;
  font-size: 12px;
}
.seller .info .info-item:last-child::before {
  display: none;
}
.seller .info .info-item:last-child::after {
  display: none;
}
/*# sourceMappingURL=src/components/seller/seller.css.map */