.header {
  position: relative;
  color: #fff;
  overflow: hidden;
  background-color: rgba(7,17,27,0.5);
}
.header .content-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px 12px 18px 24px;
}
.header .content-wrapper .avatar {
  flex: 0 0 64px;
  width: 64px;
  margin-right: 16px;
}
.header .content-wrapper .avatar img {
  border-radius: 2px;
}
.header .content-wrapper .content {
  flex: 1;
}
.header .content-wrapper .content .title {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}
.header .content-wrapper .content .title .brand {
  width: 30px;
  height: 18px;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (min-resolution: 3dppx) {
  .header .content-wrapper .content .title .brand {
    background-image: url("brand@3x.png");
  }
}
.header .content-wrapper .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  font-weight: bold;
}
.header .content-wrapper .content .description {
  margin-bottom: 8px;
  line-height: 12px;
  font-size: 12px;
}
.header .content-wrapper .content .support {
  display: flex;
  align-items: center;
}
.header .content-wrapper .content .support .support-icon {
  margin-right: 4px;
}
.header .content-wrapper .content .support .text {
  line-height: 12px;
  font-size: 10px;
}
.header .content-wrapper .support-count {
  position: absolute;
  right: 12px;
  bottom: 14px;
  display: flex;
  align-items: center;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  border-radius: 14px;
  background-color: $color-background-selector-sss;
}
.header .content-wrapper .support-count .count {
  font-size: 10px;
}
.header .content-wrapper .support-count .icon-keyboard_arrow_right {
  margin-left: 2px;
  line-height: 24px;
  font-size: 10px;
}
.header .bulletin-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  height: 28px;
  padding: 0 8px;
  background-color: rgba(7,17,27,0.2);
}
.header .bulletin-wrapper .bulletin-title {
  flex: 0 0 22px;
  width: 22px;
  height: 12px;
  margin-right: 4px;
  background-image: url("bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
}
@media (min-resolution: 3dppx) {
  .header .bulletin-wrapper .bulletin-title {
    background-image: url("bulletin@3x.png");
  }
}
.header .bulletin-wrapper .bulletin-text {
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 10px;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  filter: blur(10px);
}
/*# sourceMappingURL=src/components/v-header/v-header.css.map */