.rating-select .rating-type {
  padding: 18px 0;
  margin: 0 18px;
}
.rating-select .rating-type .block {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  line-height: 16px;
  border-radius: 1px;
  font-size: 12px;
  color: #666;
}
.rating-select .rating-type .block.active {
  color: #fff;
}
.rating-select .rating-type .block .count {
  margin-left: 2px;
}
.rating-select .rating-type .block.positive {
  background: rgba(0,160,220,0.2);
}
.rating-select .rating-type .block.positive.active {
  background: #00a0dc;
}
.rating-select .rating-type .block.negative {
  background: #ccc;
}
.rating-select .rating-type .block.negative.active {
  background: #666;
}
.rating-select .switch {
  display: flex;
  align-items: center;
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #999;
}
.rating-select .switch.on .icon-check_circle {
  color: #00b43c;
}
.rating-select .switch .icon-check_circle {
  margin-right: 4px;
  font-size: 24px;
}
.rating-select .switch .text {
  font-size: 12px;
}
