.header-detail {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  backdrop-filter: blur(10px);
  opacity: 1;
  color: #fff;
  background-color: rgba(7,17,27,0.8);
}
.header-detail.fade-enter-active,
.header-detail.fade-leave-active {
  transition: all 1s ease;
}
.header-detail.fade-enter,
.header-detail.fade-leave-active {
  opacity: 0;
  background-color: #07111b;
}
.header-detail .detail-wrapper {
  display: inline-block;
  width: 100%;
  min-height: 100%;
}
.header-detail .detail-wrapper .detail-main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.header-detail .detail-wrapper .detail-main .name {
  line-height: 16px;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}
.header-detail .detail-wrapper .detail-main .star-wrapper {
  margin-top: 18px;
  padding: 2px 0;
  text-align: center;
}
.header-detail .detail-wrapper .detail-main .title {
  display: flex;
  width: 80%;
  margin: 28px auto 24px auto;
}
.header-detail .detail-wrapper .detail-main .title .line {
  flex: 1;
  position: relative;
  top: -6px;
  border-bottom: 1px solid rgba(255,255,255,0.2);
}
.header-detail .detail-wrapper .detail-main .title .text {
  padding: 0 12px;
  font-weight: 700;
  font-size: 14px;
}
.header-detail .detail-wrapper .supports {
  width: 80%;
  margin: 0 auto;
}
.header-detail .detail-wrapper .supports .support-item {
  display: flex;
  align-items: center;
  padding: 0 12px;
  margin-bottom: 12px;
}
.header-detail .detail-wrapper .supports .support-item:last-child {
  margin-bottom: 0;
}
.header-detail .detail-wrapper .supports .support-item .support-icon {
  margin-right: 6px;
}
.header-detail .detail-wrapper .supports .support-item .text {
  line-height: 16px;
  font-size: 12px;
}
.header-detail .detail-wrapper .bulletin {
  width: 80%;
  margin: 0 auto;
}
.header-detail .detail-wrapper .bulletin .content {
  padding: 0 12px;
  line-height: 24px;
  font-size: 12px;
}
.header-detail .detail-close {
  position: relative;
  width: 30px;
  height: 30px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 30px;
}
