






































@import "~common/stylus/variable"
.cartcontrol
  display flex
  align-items center
  .cart-decrease
    display inline-block
    padding 6px
    opacity 1
    .inner
      display inline-block
      line-height 24px
      font-size $fontsize-large-xxx
      color $color-blue
      transition all 0.4s linear
      transform rotate(0)
    &.move-enter-active, &.move-leave-active
      transition all 0.4s linear
    &.move-enter, &.move-leave-to
      opacity 0
      transform translateX(24px)
      .inner
        transform rotate(180deg)
.cart-count
  width 12px
  line-height 24px
  text-align center
  font-size $fontsize-small-s
  color $color-grey
.cart-add
  display inline-block
  padding 6px
  line-height 24px
  font-size $fontsize-large-xxx
  color $color-blue
