.cube-shop-cart-list {
  bottom: 48px;
}
.cube-shop-cart-list.fade-enter,
.cube-shop-cart-list.fade-leave-active {
  opacity: 0;
}
.cube-shop-cart-list.fade-enter-active,
.cube-shop-cart-list.fade-leave-active {
  transition: all 0.3s ease-in-out;
}
.cube-shop-cart-list .move-enter,
.cube-shop-cart-list .move-leave-active {
  transform: translate3d(0, 100%, 0);
}
.cube-shop-cart-list .move-enter-active,
.cube-shop-cart-list .move-leave-active {
  transition: all 0.3s ease-in-out;
}
.cube-shop-cart-list .list-header {
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
  background-color: #f3f5f7;
}
.cube-shop-cart-list .list-header .title {
  float: left;
  font-size: 14px;
  color: #333;
}
.cube-shop-cart-list .list-header .empty {
  float: right;
  font-size: 12px;
  color: #00a0dc;
}
.cube-shop-cart-list .list-content {
  padding: 0 18px;
  max-height: 217px;
  overflow: hidden;
  background-color: #fff;
}
.cube-shop-cart-list .list-content .food {
  position: relative;
  padding: 12px 0;
  box-sizing: border-box;
}
.cube-shop-cart-list .list-content .food .name {
  line-height: 24px;
  font-size: 14px;
  color: #333;
}
.cube-shop-cart-list .list-content .food .price {
  position: absolute;
  right: 90px;
  bottom: 12px;
  line-height: 24px;
  font-weight: 700;
  font-size: 14px;
  color: #f01414;
}
.cube-shop-cart-list .list-content .food .cart-control-wrapper {
  position: absolute;
  right: 0;
  bottom: 6px;
}
